body {
    width: 100vw;
    height: 100vh;
   
  }
  
  #root {
    width: 100%;
    height: 100%;
    background-color: #58B792;
  }
  
  .App {
    display: flex; 
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  
  .square {
    width: 800px;
    height: 800px;
    display: grid;
    background-color:#282C34;
    grid-template-columns: repeat(20, 1fr);
    grid-template-rows: repeat(20, 1fr);
  }
  
  .box {
    width: 40px;
    height: 40px;
    outline: 2px solid green;
    display: inline-block;
  }
  
  .food {
    background:#FFFF00;
  }
  
  .snake {
    background:#30AD23;
  }
  
  .snakebody {
    background:#30AD23;
  }

  .marg{
    margin-right: 2%;
    margin-left: 2%;
  }

  .marg-btm{
    margin-bottom: 1%;
  }
h1, h2, h3, p{
  color: white;
  
}
